/* App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #3498db;
  color: #fff;
  padding: 1em;
  text-align: start;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2em;
}

.button {
  background-color: #2ecc71;
  color: #fff;
  padding: 0.5em 1em;
  font-size: 1em;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.button:hover {
  background-color: #27ae60;
}
